/* Type */

	body, input, select, textarea {
		// Modified by Munair
		// color: _palette(fg);
		color: _palette(darkgreytext);
		font-family: _font(family);
		font-size: 16.5pt;
		font-weight: _font(weight);
		line-height: 1.65;

		@include breakpoint(xlarge) {
			font-size: 13pt;
		}

		@include breakpoint(large) {
			font-size: 12pt;
		}

		@include breakpoint(medium) {
			font-size: 12pt;
		}

		@include breakpoint(small) {
			font-size: 12pt;
		}

		@include breakpoint(xsmall) {
			font-size: 12pt;
		}
	}

	a {
		@include vendor('transition', 'color #{_duration(transition)} ease-in-out, border-bottom-color #{_duration(transition)} ease-in-out');
		border-bottom: dotted 1px _palette(fg-light);
		color: _palette(fg-bold);
		text-decoration: none;

		&:hover {
			border-bottom-color: transparent;
			color: _palette(fg-bold) !important;
		}

		&.special:not(.button) {
			@include icon;
			border-bottom: 0;
			display: block;
			font-family: _font(family-heading);
			font-size: 0.8em;
			font-weight: _font(weight-heading-bold);
			letter-spacing: _font(kern-heading);
			margin: 0 0 _size(element-margin) 0;
			text-transform: uppercase;

			&:before {
				@include vendor('transition', 'background-color #{_duration(transition)} ease-in-out');
				border-radius: 100%;
				border: solid 2px _palette(border);
				content: '\f105';
				display: inline-block;
				font-size: 1.25em;
				height: 2em;
				line-height: 1.65em;
				margin-right: 0.85em;
				text-align: center;
				text-indent: 0.15em;
				vertical-align: middle;
				width: 2em;
			}

			&:hover {
				&:before {
					background-color: _palette(border-bg);
				}
			}

			&:active {
				&:before {
					background-color: _palette(border2-bg);
				}
			}
		}
	}

	strong, b {
		color: _palette(fg-bold);
		font-weight: _font(weight-bold);
	}

	em, i {
		font-style: italic;
	}

	p {
		margin: 0 0 _size(element-margin) 0;
	}

	h1, h2, h3, h4, h5, h6 {
		color: _palette(fg-bold);
		font-family: _font(family-heading);
		font-weight: _font(weight-heading-bold);
		letter-spacing: _font(kern-heading);
		margin: 0 0 (_size(element-margin) * 0.5) 0;
		text-transform: uppercase;
		// Modified by Munairdd
		// text-shadow: rgba(255,255,255,0.5) 0px 3px 3px;
		text-shadow: rgba(255,255,255,0.75) 2px 2px 10px;

		a {
			color: inherit;
			text-decoration: none;
		}

		span {
			font-weight: _font(weight-heading);
		}

		// Split up border and padding
		// Munair
		&.major {
			padding-bottom: 0.5em;
		}

		&.line {
			border-bottom: solid 2px _palette(border);
		}
	}

	// Appended by Munair
        // Originally, there was no h1 section styling
	// Reason: The shadow just looks nicer and I want to keep the theme going.
	h1 {
                
		text-shadow: rgba(0,0,0,0.75) 2px 2px 5px;
	}

	h2 {
                // Modified by Munair
                // Originally, there was no color or text-shadow.
                color: _palette(whitetext);
		text-shadow: rgba(0,0,0,0.75) 2px 2px 10px;
		font-size: 1.2em;
	}

	h3 {
		font-size: 0.9em;
		text-shadow: none;
	}

	h4 {
		font-size: 0.7em;
		text-shadow: none;
	}

	h5 {
		font-size: 0.7em;
		text-shadow: none;
	}

	h6 {
		font-size: 0.7em;
		text-shadow: none;
	}

	@include breakpoint(small) {
		h1 {
			text-shadow: rgba(0,0,0,0.75) 0.5px 0.5px 1px;
		}

		h2 {
			font-size: 1em;
			text-shadow: rgba(0,0,0,0.75) 1px 1px 5px;
		}

		h3 {
			font-size: 0.8em
		}
	}

	sub {
		font-size: 0.8em;
		position: relative;
		top: 0.5em;
	}

	sup {
		font-size: 0.8em;
		position: relative;
		top: -0.5em;
	}

	blockquote {
		border-left: solid 4px _palette(border);
		font-style: italic;
		margin: 0 0 _size(element-margin) 0;
		padding: (_size(element-margin) / 4) 0 (_size(element-margin) / 4) _size(element-margin);
	}

	code {
		background: _palette(border-bg);
		border-radius: _size(border-radius);
		border: solid 2px _palette(border);
		font-family: _font(family-fixed);
		font-size: 0.9em;
		margin: 0 0.25em;
		padding: 0.25em 0.65em;
	}

	pre {
		-webkit-overflow-scrolling: touch;
		font-family: _font(family-fixed);
		font-size: 0.9em;
		margin: 0 0 _size(element-margin) 0;

		code {
			display: block;
			line-height: 1.75em;
			padding: 1em 1.5em;
			overflow-x: auto;
		}
	}

	hr {
		border: 0;
		border-bottom: solid 2px _palette(border);
		margin: (_size(element-margin) * 1.25) 0;

		&.major {
			margin: (_size(element-margin) * 2) 0;
		}
	}

	.align-left {
		text-align: left;
	}

	.align-center {
		text-align: center;
	}

	.align-right {
		text-align: right;
	}

	// Styling by Munair.
	section {
        	header {
			.inner {
				p { 
					color: _palette(accent); 
					margin-bottom: 0.1em;
					padding-bottom: 0.1em;
					text-shadow: rgba(0,0,0,0.75) 2px 2px 5px;
					@include breakpoint(small) {
						text-shadow: rgba(0,0,0,0.75) 1px 1px 2px;
					}
				}
			}
		}
	}
