// Misc.
	$misc: (
		z-index-base:			10000,
		max-wrapper-styles:		6
	);

// Duration.
	$duration: (
		menu:				0.55s,
		transition:			0.2s
	);

// Size.
	$size: (
		border-radius:			5px,
		element-height:			3em,
		element-margin:			2em,
		inner:				55em,
		section-spacing:		(
			large:				3em,
			medium:				2em,
			small:				1.75em
		),
		wrapper-edges:			(
			large:				6.5em,
			medium:				4.75em,
			small:				2.5em
		)
	);

// Font.
	$font: (
		family:				('Source Sans Pro', Helvetica, sans-serif),
		family-fixed:			('Courier New', monospace),
		family-heading:			(Raleway, Helvetica, sans-serif),
		weight:				300,
		weight-bold:			600,
		weight-heading:			800,
		weight-heading-bold:		900,
		kern-heading:			0.1em
	);

// Palette.
	$palette: (
		bg:				#ffffff,
		lightbg:			#ffffff,
		darkgreybg:			#5f5f5f,
		darkgreytext:			#333333,
		whitetext:			#ffffff,
		fg:				#000000,
		fg-bold:			#2e3141,
		fg-light:			rgba(0,0,0,0.35),
		border:				rgba(0,0,0,0.125),
		border-bg:			rgba(0,0,0,0.025),
		border2:			rgba(0,0,0,0.25),
		border2-bg:			rgba(0,0,0,0.075),
		feature-bg:			#f9f9f9,
		accent:				#ff9900,
	);
