/* Featured Image */

	.featuredimage-wrapper {
		position: relative; 
		display: inline-block; 
		margin: 5%; 
		padding: 0; 
		min-width: 250px; 
                border: 1px solid _palette(bg);
                box-shadow: 0px 1px 1px 0px rgba(_palette(fg), 0.3);
		border-radius: 100%; 
		overflow: hidden;

		> .label {
			display: none;
		}
	}

	.featuredimage {
		height: 250px; 
		background-size: auto 250px; 
		background-position: center; 
		background-repeat: no-repeat; 
		background-image: url(https://1.gravatar.com/avatar/4dcf745d1a32a6f20d447d0cf21a5827?s=96&amp;d=identicon&amp;r=G);
		transition: all 1s ease-out;

		> .label {
			display: none;
		}

		&:hover, 
		&:focus {
			background-size: auto 105%;
		}
	}
