/* Basic */

	// MSIE: Required for IEMobile.
		@-ms-viewport {
			width: device-width;
		}

	// MSIE: Prevents scrollbar from overlapping content.
		body {
			-ms-overflow-style: scrollbar;
		}

	// Ensures page width is always >=320px.
		@include breakpoint(xsmall) {
			html, body {
				min-width: 320px;
			}
		}

	body {
		// Modified by Munair
		// background-color:		_palette(bg);
		background-color:		_palette(darkgreybg);

		// Modified by Munair
		// background-image:		linear-gradient(to top, transparentize(_palette(bg), 0.2), transparentize(_palette(bg), 0.2)), url('../../images/bg.jpg');
		background-image:		linear-gradient(to top, transparentize(_palette(darkgreybg), 0.02), transparentize(_palette(darkgreybg), 0.02)), url('../../images/bg.jpg');

		background-size:		auto, cover;

		background-attachment:		fixed, fixed;

		background-position:		center, center;

		// Prevents animation/transition "flicker" on page load.
		// Automatically added/removed by js/main.js.
			&.is-loading {
				*, *:before, *:after {
					@include vendor('animation', 'none !important');
					@include vendor('transition', 'none !important');
				}
			}

	}
